.btn-menu {
  position: relative;
  float: right;
  margin: 28px -15px 0 0;
  width: 52px;
  height: 56px;
  background: none;
  cursor: pointer;
  pointer-events: initial;
  z-index: 101;
  display: none;
}

@media only screen and (max-width: 767px) {
