.header.is-open-menu .btn-menu:before {
  transform: translate3d(3px, 0, 0) rotate(45deg);
}

.header.is-open-menu .btn-menu:after {
  transform: translate3d(3px, 0, 0) rotate(-45deg);
}

.header.is-open-menu .btn-menu span {
  opacity: 0;
  transform: scaleX(0.5);
}

.btn-menu span, .btn-menu:before, .btn-menu:after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  border-top: 2px solid #0D2A63;
  width: 20px;
}

.btn-menu:before, .btn-menu:after {
  content: '';
  transition: transform 0.3s;
}

.btn-menu span {
  margin-top: -1px;
  transition: opacity 0.3s, transform 0.3s;
  transform-origin: left center;
}

.btn-menu:before {
  margin-top: -8px;
  transform-origin: left top;
}

.btn-menu:after {
  margin-top: 6px;
  transform-origin: left bottom;
