.card {
  position: relative;
  overflow: hidden;
  border-radius: 20px 20px 10px 10px;
  width: 230px;
  height: 340px;
  background: #fff;
  box-shadow: 0 42px 40px -20px rgba(107, 162, 255, 0.26);
  font-family: "Roboto", "Open Sans", sans-serif;
  text-align: center;
  vertical-align: top;
  display: inline-block;
}

.card__cover {
  height: 230px;
}

.card__cover-in {
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.card__content {
  position: relative;
  z-index: 2;
  padding: 42px 15px 20px;
}

.card__title, .card__subtitle {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.1875;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.card__title.is-active, .card__subtitle.is-active {
  color: #fff;
}

.card__title {
  margin-bottom: 4px;
  color: #0D2A63;
}

.card__subtitle {
  color: #5296A7;
}

.card__toggle {
  position: absolute;
  z-index: 10;
  left: 20px;
  bottom: 75px;
  transform: translate3d(0, 0, 0);
}

.card__toggle-button {
  position: relative;
  border: 8px solid rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  width: 70px;
  height: 70px;
  background: #6AA2FF;
  background-clip: padding-box;
  text-align: center;
  font-size: 0;
  transform: translate3d(0, 0, 0);
  transition: border-color 0.1s, background 0.15s;
}

.card__toggle-button:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.card__toggle-button.is-active {
  border-color: rgba(255, 255, 255, 0);
}

.card__toggle-button.is-active .card__toggle-play {
  opacity: 0;
}

.card__toggle-button.is-active .card__toggle-pause {
  opacity: 1;
  transform: rotate(0);
  transition: opacity 0.2s 0.2s, transform 0.2s 0.25s;
}

.card__toggle-button.is-transparent {
  background: none;
}

.card__toggle-play, .card__toggle-pause {
  position: absolute;
  top: 50%;
  left: 50%;
  fill: #fff;
  transition: opacity 0.2s, transform 0.2s;
}

.card__toggle-pause {
  margin: -8px 0 0 -6px;
  opacity: 0;
  font-size: 16px;
  transform: rotate(-45deg);
}

.card__toggle-play {
  margin: -9px 0 0 -8px;
  font-size: 18px;
  transform: translate3d(2px, 0, 0);
}

.card__overlay {
  position: absolute;
  top: 115px;
  left: 50%;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  opacity: 0;
  background: #6AA2FF;
  box-shadow: 0 0 10px #6AA2FF;
  transform: translate3d(-50%, -50%, 0);
}

.card__overlay.is-active {
  opacity: 1;
}

.card__controls {
  position: absolute;
  top: 45px;
  left: 0;
  right: 0;
}

.card__thumb {
  margin: 0 auto;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  opacity: 0.2;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.card__thumb, .card__control {
  transform: scale(0);
}

.card__control {
  position: absolute;
  top: 50%;
  margin-top: -25px;
  width: 50px;
  height: 50px;
  background: none;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.card__control-icon {
  display: inline-block;
  vertical-align: middle;
  fill: #fff;
}

.card__prev {
  left: 30px;
}

.card__next {
  right: 30px;
}

.card__progressbar {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 80px;
  height: 30px;
}

.card__progress-default {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  border-bottom: 4px solid rgba(255, 255, 255, 0.4);
  transform: translate3d(-100%, -50%, 0);
}

.card__progress-active {
  position: absolute;
  top: 50%;
  left: 0;
  right: 20px;
  margin-top: -2px;
  height: 4px;
  opacity: 0;
  background: #fff;
  transform: translate3d(-100%, 0, 0);
  transition: opacity 0.25s;
}

.card__progress-active.is-active {
  opacity: 1;
}

.card__progress-active:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -10px;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background: #fff;
  box-shadow: -8px 0 7px -4px rgba(0, 0, 0, 0.21);
}

.hero {
